import React from 'react';

const ResendTokenSuccess = () => (
  <div>
    <div style={{marginTop: '20px', textAlign: 'center'}}>
      <h3>Success</h3>
      <p>You should soon receive an email with confirmation token.</p>
    </div>
  </div>
)

export default ResendTokenSuccess;



